import Vue from 'vue'
import App from './App'

import './assets/sass/style.scss'

Vue.config.productionTip = false

/* eslint-disable no-new */
export const EventBus = new Vue();


new Vue({
  el: '#app',
  components: { App },
  template: '<App/>'
})




